import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { Router } from '@angular/router';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {
  currentTime: string;
  currentDate: string;
  apiUrl = environment.apiUrl;
  userid: string | null = localStorage.getItem('userid');
  selector='5c';
  summary:any;
  orders:any;
  loader=false;
  daily:any;
  stats:any;
  userstatview=false;
  usercrudview=true;
  groupedProducts: any[] = [];
  clockinview=true;
  summaryview=false;
  users:any;
  selectedUser: any = {
    id:'',
    username: '',
    userid:'',
    role:'',
    password:''
  };
  activePage: string = 'clockin'; // Set the default active page
  servicetotal=0.00;
  apiResponse:any;clockin:any;
  closedayview=false;
  constructor(
    private http: HttpClient,
    private router: Router,
    private cdr: ChangeDetectorRef // Inject ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.updateTime();
    this.checkauth();
   this.getreceipts();
   this.getusers();
  }
  viewpage(pagename:string){
if(pagename=="crud"){
  this.userstatview=false;
  this.usercrudview=true;
}
if(pagename=="stats"){
  this.userstatview=true;
  this.usercrudview=false;
}
  }
  checkauth() {
    const userid = localStorage.getItem('userid');
    const username = localStorage.getItem('username');
    const token = localStorage.getItem('token');

    if (userid && username && token) {
      const data = {
        token: token,
        userid: userid,
        username: username
      };

      const headers = new HttpHeaders({
        'Content-Type': 'application/json'
      });

      this.http.post(this.apiUrl + 'checkauth', data, { headers }).subscribe(
        (response: any) => {
          if (response.error === false) {
            if(response.clockin===false){
              this.router.navigate(['clockin']);
            }
            //this.router.navigate(['']);
          } else {
            this.router.navigate(['']);
          }
        },
        (error) => {
          console.error('Error during login:', error);
        }
      );
    } else {
      this.router.navigate(['']);
    }
  }
  getusers(){
    const token = localStorage.getItem('token');
    const queryParams = { params: { token: token } };

    this.http.get(this.apiUrl + 'superusers', queryParams).subscribe(
      (response: any) => {
        if (response.error === false) {
          this.users = response.users;
          // Handle successful response
        } else {
          // Handle error response
          this.router.navigate(['']);
        }
      },
      (error) => {
        console.error('Error during login:', error);
      }
    );
  }
  editUser(user: any) {
    const headers = new HttpHeaders({
      'Content-Type': 'application/json'
    });
    
    this.selectedUser.username=user.username;
    this.selectedUser.id=user.id;
    this.selectedUser.userid=user.userid;
    this.selectedUser.password=user.user_password;
    this.selectedUser.role=user.role;
  }
  getreceipts(){
    this.http.get(this.apiUrl + 'getuser').subscribe(
      (response: any) => {
        if (response.error === false) {
         this.orders=response.today;
         this.daily=response.daily;
          //this.router.navigate(['']);
        } else {
          this.router.navigate(['']);
        }
      },
      (error) => {
        console.error('Error during login:', error);
      }
    );
  }
  addUser(user: any) {
    this.loader=true;
    const headers = new HttpHeaders({
      'Content-Type': 'application/json'
    });

    this.http.post(`${this.apiUrl}superusers`, user, { headers }).subscribe(() => {
      this.getusers();
      this.selectedUser.username='';
      this.selectedUser.id='';
      this.selectedUser.userid='';
      this.selectedUser.password='';
      this.selectedUser.role='';
     
       setTimeout(() => {
        this.loader = false; // Hide the loader
      }, 500);
    });
  }
  updateTime() {
    const updateClock = () => {
      const now = new Date();
      this.currentDate = now.toLocaleDateString();
      this.currentTime = now.toLocaleTimeString();
      this.cdr.detectChanges(); // Trigger change detection manually
    };
    // Call updateClock initially
    updateClock();
    // Set up a timer to update the time every second
    setInterval(updateClock, 1000);
  }

}
