import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { ConfirmationDialogComponent } from '../confirmation-dialog/confirmation-dialog.component';
@Component({
  selector: 'app-receipts',
  templateUrl: './receipts.component.html',
  styleUrls: ['./receipts.component.css']
})
export class ReceiptsComponent implements OnInit {
  currentTime: string;
  currentDate: string;
  apiUrl = environment.apiUrl;
  userid: string | null = localStorage.getItem('userid');
  selector='5c';
  summary:any;
  orders:any;
  phones:any;
  stats:any;
  ordertable=true;
  groupedProducts: any[] = [];
  clockinview=true;
  summaryview=false;
  orderview=false;
  phonetable=false;
  orderinfo:any;
  settings:any;
  activePage: string = 'clockin'; // Set the default active page
  servicetotal=0.00;
  apiResponse:any;clockin:any;
  closedayview=false;
  constructor(
    private http: HttpClient,
    private dialog: MatDialog,
    private router: Router,
    private cdr: ChangeDetectorRef // Inject ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.updateTime();
    this.checkauth();
   this.getreceipts();
   this.getreports();
   this.getsettings();
  }
  getreports(){
    this.http.get(this.apiUrl + 'getreport').subscribe(
      (response: any) => {
        if (response.error === false) {
        
          this.stats=response.stats;
        
         
          //this.router.navigate(['']);
        } else {
          this.router.navigate(['']);
        }
      },
      (error) => {
        console.error('Error during login:', error);
      }
    );
  }
  getsettings(){
    this.http.get(`${this.apiUrl}get-general-settings`).subscribe((data: any) => {
      // Load general settings data from the server response
      this.settings = data;
    });
  }
  checkauth() {
    const userid = localStorage.getItem('userid');
    const username = localStorage.getItem('username');
    const token = localStorage.getItem('token');

    if (userid && username && token) {
      const data = {
        token: token,
        userid: userid,
        username: username
      };

      const headers = new HttpHeaders({
        'Content-Type': 'application/json'
      });

      this.http.post(this.apiUrl + 'checkauth', data, { headers }).subscribe(
        (response: any) => {
          if (response.error === false) {
            if(response.clockin===false){
              this.router.navigate(['clockin']);
            }
            //this.router.navigate(['']);
          } else {
            this.router.navigate(['']);
          }
        },
        (error) => {
          console.error('Error during login:', error);
        }
      );
    } else {
      this.router.navigate(['']);
    }
  }
  viewpage(orderid:string){
    this.ordertable=false;
    this.orderview=true;
    this.phonetable=false;
    const data = {
      orderid: orderid
    };

    const headers = new HttpHeaders({
      'Content-Type': 'application/json'
    });

    this.http.post(this.apiUrl + 'getorder', data, { headers }).subscribe(
      (response: any) => {
        if (response.error === false) {
          try {
            // Parse the order_items JSON string into an array
            response.orderinfo.order_items = JSON.parse(response.orderinfo.order_items);
          } catch (error) {
            console.error('Error parsing order_items:', error);
          }
  
          this.orderinfo = response.orderinfo;
          
          console.log(this.orderinfo.order_items);
          //this.router.navigate(['']);
        } else {
          this.router.navigate(['']);
        }
      },
      (error) => {
        console.error('Error during login:', error);
      }
    );
  }
  getcashtotal(orderinfo){
    return parseFloat(orderinfo.cash) + parseFloat(orderinfo.changeamount);
  }
  getFormattedData(data: any) {
    debugger;
    const parsedData = JSON.parse(data);
  
    console.log(parsedData);
    if (parsedData && parsedData.length > 0) {
      const formattedData = parsedData.map(item => `${item.modeofpayment} : ${item.amount}`).join(', ');
      return formattedData;
    }
    return '';
  }
  getreceipts(){
    this.orderinfo=false;
    this.ordertable=true;
    this.phonetable=false;
    this.http.get(this.apiUrl + 'getreceipt').subscribe(
      (response: any) => {
        if (response.error === false) {
         this.orders=response.orders;
      
          //this.router.navigate(['']);
        } else {
          this.router.navigate(['']);
        }
      },
      (error) => {
        console.error('Error during login:', error);
      }
    );
  }
  getphonereceipts(){
    this.orderinfo=false;
    this.ordertable=false;
    this.phonetable=true;
    this.http.get(this.apiUrl + 'getphonereceipt').subscribe(
      (response: any) => {
        if (response.error === false) {
         this.phones=response.orders;
          //this.router.navigate(['']);
        } else {
          this.router.navigate(['']);
        }
      },
      (error) => {
        console.error('Error during login:', error);
      }
    );
  }
  getAvailablePhones() {
    this.orderinfo = false;
    this.ordertable = false;
    this.phonetable = true;
  
    // Make an HTTP request to get all phone receipts
    this.http.get(`${this.apiUrl}get-available-phones`).subscribe((data: any) => {
      // Load general settings data from the server response
      this.phones = data;
    });
  }
  
  calculateTotal(order: any): number {
    return parseFloat(order.card) + parseFloat(order.cash) + parseFloat(order.giftcard) + parseFloat(order.pointsRedeem);
  }
  getservicereceipts(){
    this.orderinfo=false;
    this.ordertable=true;
    this.phonetable=false;
    this.http.get(this.apiUrl + 'getservicereceipt').subscribe(
      (response: any) => {
        if (response.error === false) {
         this.orders=response.orders;
          //this.router.navigate(['']);
        } else {
          this.router.navigate(['']);
        }
      },
      (error) => {
        console.error('Error during login:', error);
      }
    );
  }
  getonereceipt(orderId:string){
  
    const data = {
   id:orderId
    };

    const headers = new HttpHeaders({
      'Content-Type': 'application/json'
    });
    this.http.post(this.apiUrl + 'getonereceipt', data, { headers }).subscribe(
      (response: any) => {
        if (response.error === false) {
          console.log(response.receipt);
            //c Parse the order_items JSON string into an array
            this.printReceipt(response.receipt,response.type);

         
  
          
          //this.router.navigate(['']);
        } else {
         alert("Invalid Id");
        }
      },
      (error) => {
        console.error('Error during login:', error);
      }
    );
  }
  printHtml(receiptHtml: string): void {
    const url = 'http://127.0.0.1:5000/print_html';
    const headers = new HttpHeaders({ 'Content-Type': 'application/json' });
    const body = { htmlContent: receiptHtml,printername:this.settings.printername };
  
    this.http.post(url, body, { headers, observe: 'response' }).subscribe(
      (response) => {
        if (response.status === 200) {
          console.log('Print request sent successfully.');
        } else {
          console.error('Failed to send print request.');
        }
      },
      (error) => {
        console.error('Error while sending print request:', error);
      }
    );
  }
  myfooter='';
  printReceipt(receiptData: any,type:string) {
    if(type=="Product"){
    this.myfooter=this.settings.salefooter;
    }
    if(type=="Service"){
      this.myfooter=this.settings.repairfooter;
      }
      if(type=="Phone"){
        this.myfooter=this.settings.phonefooter;
        }
    // Construct the receipt HTML template using the provided receiptData
    const productsHtml = receiptData.items.map((item: any) => `
      <tr>
        <td>${item.quantity}</td>
        <td>${item.product_name}</td>
        <td>${item.unit_price}</td>
        <td></td>
        <td>${item.line_total}</td>
      </tr>`).join('');
  
    const receiptHtml = `
      <!DOCTYPE html>
      <html>
      <head>
        <title>Receipt</title>
        <style type="text/css" media="print">
          @page {
            size: 72mm;
            margin: 0;
            height: max-content;
          }
          table {
            width: 100%;
          }
          table tr td {
            text-align: center;
          
          }
          .table-header tr {
           border:5px solid black;
           }
          hr{
            line-height:2px;
          }
        </style>
      </head>
      <body>
        <h2>${receiptData.order_id}</h2>
        <p>${this.settings.storename}</p>
        <p>${this.settings.address} • ${this.settings.city} • ${this.settings.state} • ${this.settings.postalcode}</p>
        <p>${this.settings.phone}</p>
        <table class="table">
          <thead class="table-header">
            <tr>
              <th>QTY</th>
              <th>DESCRIPTION</th>
              <th>PRICE</th>
              <th>DISC.</th>
              <th>TOT</th>
            </tr>
          </thead>
          <tbody>
            ${productsHtml}
          </tbody>
        </table>
        <hr><hr>
        <table>
          <tbody>
            <tr>
              <td>${receiptData.order_date}</td>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td><b>SUB-TOT:</b></td>
              <td>${receiptData.subtotal_amount}</td>
            </tr>
            <tr>
              <td><b>REMARK :</b></td>
              <td> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td></td>
              <td><b>HST:</b></td>
              <td>${receiptData.tax_amount}</td>
            </tr>
            <tr>
            <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
            <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
            <td></td>
            <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
            <td><b>TOTAL :</b></td>
            <td>${receiptData.total_amount}</td>
          </tr>
            <tr>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td></td>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td><b>TOTAL DUE :</b></td>
              <td>${receiptData.due_amount}</td>
            </tr>
            <tr>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td></td>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td><b>CASH :</b></td>
              <td>${receiptData.cash_payment}</td>
            </tr>
            <tr>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td></td>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td><b>Debit :</b></td>
              <td>${receiptData.card_payment}</td>
            </tr>
            <tr>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td></td>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td><b>CHANGE :</b></td>
              <td>${receiptData.change_amount}</td>
            </tr>
          </tbody>
        </table>
        <hr><hr>
        <p>${receiptData.order_status}</p>
        <p>${this.settings.salefooter}</p>
      </body>
      </html>
    `;
  
   //this.printHtml(receiptHtml);

   const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
    data: 'Print Receipt?',
  });
// Assuming dialogRef.afterClosed() returns an observable
dialogRef.afterClosed().subscribe((result: boolean) => {
  if (result) {
    const printWindow = window.open('');
    if (printWindow) {
      printWindow.document.write(receiptHtml);

      // Trigger the print dialog
      printWindow.print();

      // Close the print preview window after printing
      setTimeout(() => {
        printWindow.document.close();
        printWindow.close();
      }, 500); // Adjust the delay time as needed
    } else {
      // Handle if window.open fails
      console.error('Failed to open print window');
    }
  } else {
    // User clicked "No" or closed the dialog
  }
});
    
  }

  updateTime() {
    const updateClock = () => {
      const now = new Date();
      this.currentDate = now.toLocaleDateString();
      this.currentTime = now.toLocaleTimeString();
      this.cdr.detectChanges(); // Trigger change detection manually
    };
    // Call updateClock initially
    updateClock();
    // Set up a timer to update the time every second
    setInterval(updateClock, 1000);
  }

}
