import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { Router } from '@angular/router';

@Component({
  selector: 'app-customers',
  templateUrl: './customers.component.html',
  styleUrls: ['./customers.component.css']
})
export class CustomersComponent implements OnInit {
  apiUrl = environment.apiUrl;
  
customerview=false;
userid = localStorage.getItem('userid');
rewardData: any = {
  userid: this.userid,
  customerid: '',
  orderid: '',
  reason: '',
  points: ''
};
rewardview=false;
  constructor(
    private http: HttpClient,
    private router: Router,
    private cdr: ChangeDetectorRef // Inject ChangeDetectorRef
  ) {}
  customers:any[];
  selectedCustomer: any = null; // This will hold the customer you're editing

  ngOnInit(): void {
    this.getcustomers();
   
  }
  getcustomers(){
    this.customerview=true;
    this.rewardview=false;
    this.http.get(this.apiUrl+'getcustomers').subscribe(
      (response:any) => {
        // Handle the response from the server
        if (response.error === false) {
           this.customers = response.customers;
        } else {
          this.router.navigate(['']);
        }
      },
      (error) => {
        // Handle any errors
        console.error('Error during login:', error);
      }
    );
  }
  viewrewards(){
    this.customerview=false;
    this.rewardview=true;
  }
  rewardmessage='';
  addRewards() {
    this.http.post(this.apiUrl + 'addrewards', this.rewardData).subscribe(
      (response: any) => {
        // Handle the response from the server
        if (response.error === false) {
      this.rewardmessage=response.message;
      this.rewardData={
        userid: this.userid,
        customerid: '',
        orderid: '',
        reason: '',
        points: ''
      };
          // Reward added successfully, do something here
        } else {
          // Handle errors if needed
        }
      },
      (error) => {
        // Handle any errors
        console.error('Error adding reward:', error);
      }
    );
  }
  editCustomer(customer: any) {
    this.selectedCustomer = { ...customer };
  }
  updateCustomer() {
    console.log(this.selectedCustomer);
    
    this.http.post(this.apiUrl+'editcustomer', this.selectedCustomer).subscribe(
      (response: any) => {
        if (response.error === false) {
          // Handle success

         this.getcustomers();
         this.selectedCustomer=null;
        } else {
          // Handle error
          console.log('Error:', response.error);
        }
      },
      (error) => {
        // Handle HTTP error
        console.error('HTTP Error:', error);
      }
    );

  }

  cancelEdit() {
    this.selectedCustomer = null;
  }
  deleteCustomer(categoryId: number) {
    const headers = new HttpHeaders({
      'Content-Type': 'application/json'
    });
  
    this.http.post(`${this.apiUrl}superdeletecustomers/${categoryId}`, null, { headers }).subscribe(() => {
      this.getcustomers();
    });
  }
}
