import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';


import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { ServiceWorkerModule } from '@angular/service-worker';
import { environment } from '../environments/environment';
import { HomeComponent } from './home/home.component';
import { SalesComponent } from './sales/sales.component';
import { LoginComponent } from './login/login.component';
import { HttpClientModule } from '@angular/common/http';
import { ReactiveFormsModule } from '@angular/forms';
import { FormsModule } from '@angular/forms';
import { ReportsComponent } from './reports/reports.component';
import { SettingsComponent } from './settings/settings.component';
import { CustomersComponent } from './customers/customers.component';
import { ReceiptsComponent } from './receipts/receipts.component';
import { UsersComponent } from './users/users.component';
import { RefundComponent } from './refund/refund.component';
import { ClockinComponent } from './clockin/clockin.component';
import { ClockoutComponent } from './clockout/clockout.component';
import { GlobalsettingsComponent } from './globalsettings/globalsettings.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ConfirmationDialogComponent } from './confirmation-dialog/confirmation-dialog.component';
import { MatDialogModule } from '@angular/material/dialog'; // Import MatDialogModule


import { MatProgressBarModule} from '@angular/material/progress-bar';


import { MatButtonModule } from '@angular/material/button';


import { IKeyboardLayouts, keyboardLayouts, MAT_KEYBOARD_LAYOUTS, MatKeyboardModule } from 'angular-onscreen-material-keyboard';
import { MatSnackBarModule } from '@angular/material/snack-bar';


const customLayouts: IKeyboardLayouts = {
  ...keyboardLayouts,
  'numbers': {
    'name': 'Numbers and Operators Layout',
    'keys': [
      [['1'], ['2'], ['3'], ['+']],
      [['4'], ['5'], ['6'], ['-']],
      [['7'], ['8'], ['9'], ['*']],
      [['.'], ['0'], ['/'], ['%']],
      [['Backspace'], ['Enter'] ]// Assuming these are placed at the bottom right of the keyboard
    ],
    'lang': ['en-US']
  }
};


@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    SalesComponent,
    LoginComponent,
    ReportsComponent,
    SettingsComponent,
    CustomersComponent,
    ReceiptsComponent,
    UsersComponent,
    RefundComponent,
    ClockinComponent,
    ClockoutComponent,
    GlobalsettingsComponent,
    ConfirmationDialogComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    ReactiveFormsModule,
    HttpClientModule,
    FormsModule,
    MatDialogModule,
    BrowserAnimationsModule,
    MatButtonModule,
    MatKeyboardModule,
    MatSnackBarModule,

    MatIconModule,
    CommonModule, 
    MatProgressBarModule,  
    ServiceWorkerModule.register('ngsw-worker.js', { enabled: environment.production }), BrowserAnimationsModule
  ],
  providers: [
    { provide: MAT_KEYBOARD_LAYOUTS, useValue: customLayouts }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
