import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './login/login.component'; 
import { HomeComponent } from './home/home.component'; // Import your HomeComponent
import { SalesComponent } from './sales/sales.component'; // Import your sales

import { GlobalsettingsComponent } from './globalsettings/globalsettings.component';

import { ReportsComponent } from './reports/reports.component';
import { SettingsComponent } from './settings/settings.component';
import { CustomersComponent } from './customers/customers.component';
import { ReceiptsComponent } from './receipts/receipts.component';
import { UsersComponent } from './users/users.component';
import { RefundComponent } from './refund/refund.component';

import { ClockinComponent } from './clockin/clockin.component';
import { ClockoutComponent } from './clockout/clockout.component';
const routes: Routes = [
  { path: '', component: LoginComponent }, // Set the path to an empty string for the home page
  { path: 'home', component: HomeComponent },
  { path: 'sales', component: SalesComponent }, // Set the path to an empty string for the home page
  { path: 'reports', component: ReportsComponent },
  { path: 'settings', component: SettingsComponent },
  { path: 'customers', component: CustomersComponent },
  { path: 'receipts', component: ReceiptsComponent },
  { path: 'users', component: UsersComponent },
  { path: 'refund', component: RefundComponent },
  { path: 'clockin', component: ClockinComponent },
  { path: 'clockout', component: ClockoutComponent },
  { path: 'global', component: GlobalsettingsComponent }
  // Define other routes as needed
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
