//search
  onSearch(event: any) {
    const searchText = event.target.value;

    if (searchText.trim() === '') {
      // Handle empty search
      this.search = [];
      return;
    }
    this.loader=true;

    // Call your API with the search text
    this.http.get<any[]>(`${this.apiUrl}products/search?searchTerm=${searchText}`).subscribe(
      (response: any[]) => {
        this.searchsview=true;
        this.search = response;
       
        console.log(this.search);
        setTimeout(() => {
          this.loader = false; // Hide the loader
        }, 500);
      },
      (error) => {
        console.error('Error:', error);
      }
    );
  }
  clearsearch(){
    this.search = [];
    this.searchProductText='';
  }
